article.search-author{margin:2em 0;cursor:pointer;display:flex}article.search-author figure{width:10%;aspect-ratio:1;min-width:100px;height:max-content}article.search-author figure img{width:100%;height:100%;object-fit:cover}article.search-author section.right{flex-grow:1;display:flex;justify-content:space-between;align-items:none;flex-direction:column;margin-left:.5em}article.search-author section.right p.author{font-weight:200;font-size:1.75rem}article.search-author section.right section.details{display:flex;flex-wrap:wrap}article.search-author section.right section.details div.rate{padding:.4em 1em;border-radius:.25em;background:rgba(30,30,30,.8);margin-right:.25em;display:flex}article.search-author section.right section.details div.rate span{display:block;height:30px;width:30px;margin-right:.25em}article.search-author section.right section.details div.rate p{font-size:.85rem;font-weight:500}@media(max-width: 480px){article.search-author{background:rgba(20,20,20,.5);padding:.4em}article.search-author figure{display:none}article.search-author section.right{margin:0}article.search-author section.right section.details{margin-top:.5em}article.search-author section.right section.details div.rate{padding:.4em;background:rgba(25,25,25,.8)}}