main.search{margin-top:75px;min-height:100vh;padding:2em}main.search section.container{margin-top:3em}main.search section.search-entry{display:flex;justify-content:space-between;align-items:center;flex-direction:row}main.search section.search-entry section.query p.results{font-weight:700;font-size:clamp(1.5rem, 7vw, 2rem)}main.search section.search-entry section.query p.query{font-size:clamp(0.95rem, 5vw, 1.1rem);color:#d7d7d7}main.search section.search-entry section.searchbar{width:40%}main.search section.search-entry section.searchbar div{width:100%;position:relative}main.search section.search-entry section.searchbar div input{width:100%;border:0;outline:0;padding:.75em;font-size:clamp(0.85rem, 3vw, 0.95rem);background:rgba(30,30,30,.9);border-radius:.15em;color:#b4b4b4}main.search section.search-entry section.searchbar div input::placeholder{opacity:.25;font-size:.8rem}main.search section.search-entry section.searchbar div span{display:block;height:30px;width:30px;position:absolute;right:.5em;top:50%;translate:0 -50%}@media(max-width: 768px){main.search{padding:2em .5em}main.search section.search-entry{flex-direction:column}main.search section.search-entry section.query{width:100%;margin-bottom:1.25em}main.search section.search-entry section.searchbar{width:100%}}