article.search-album{display:flex;margin-bottom:2em;cursor:pointer}article.search-album section.right{display:flex;justify-content:space-between;align-items:none;flex-direction:column;padding:.5em}article.search-album section.right p.author{color:#b4b4b4;font-size:clamp(1rem, 5vw, 1.25rem)}article.search-album section.right p.name{font-size:clamp(1.25rem, 5vw, 1.75rem);margin-bottom:.5em}article.search-album section.right p.total{margin-bottom:.25em;color:#d7d7d7;font-size:clamp(0.8rem, 4vw, 0.95rem)}article.search-album section.right section.rating{display:flex}article.search-album section.right section.rating div{display:flex;justify-content:center;align-items:center;flex-direction:column;margin-right:.75em;background:rgba(30,30,30,.8);padding:.5em clamp(.5em,5vw,1.5em)}article.search-album section.right section.rating div span{display:block;height:clamp(25px,6vw,35px);width:clamp(25px,6vw,35px)}article.search-album section.right section.rating div p{font-size:clamp(0.8rem, 4vw, 0.9rem)}article.search-album figure{width:20%;height:max-content;margin-right:.25em;aspect-ratio:1}article.search-album figure img{width:100%;height:100%;object-fit:cover}@media(max-width: 480px){article.search-album{position:relative}article.search-album figure{position:absolute;right:0;top:0;width:50px}article.search-album section.right{width:100%}article.search-album section.right section.rating div{flex-grow:1;margin-right:.25em}}