article.search-article{margin:2em 0;cursor:pointer;display:flex}article.search-article figure{width:22%;aspect-ratio:1.5;min-width:150px;height:max-content}article.search-article figure img{width:100%;height:100%;object-fit:cover}article.search-article section.right{display:flex;justify-content:space-between;align-items:none;flex-direction:column;flex-grow:1;padding:.75em;background:rgba(20,20,20,.5)}article.search-article section.right p.text{color:#b4b4b4;width:70%;font-size:clamp(0.8rem, 4vw, 0.9rem);margin:.5em 0}article.search-article section.right div.wrap{width:100%;display:flex;justify-content:space-between;align-items:none;flex-direction:row}article.search-article section.right div.wrap:nth-child(3){align-items:flex-end}article.search-article section.right div.tags{display:flex}article.search-article section.right div.tags span{margin-left:.5em;font-size:clamp(0.8rem, 4vw, 1rem);color:#d7d7d7;padding:.35em;background:rgba(30,30,30,.75);border-radius:.25em}article.search-article section.right p.title{width:80%;font-weight:600;font-size:clamp(1rem, 6vw, 1.75rem)}article.search-article section.right p.category,article.search-article section.right p.date{color:#b4b4b4;font-size:clamp(0.8rem, 4vw, 0.95rem)}@media(max-width: 768px){article.search-article{flex-direction:column}article.search-article figure{width:100%}article.search-article section.right div.wrap:last-of-type{align-items:center}article.search-article section.right p.text{width:100%;margin-bottom:1.5em}}