article.search-track{display:flex;justify-content:space-between;align-items:center;flex-direction:row;cursor:pointer;padding:1em;background:rgba(20,20,20,.5);margin:2em 0}article.search-track section.left{width:40%}article.search-track section.left p.author{color:#b4b4b4;font-weight:500;font-size:.9rem}article.search-track section.left p.name{margin:.25em 0 .75em 0;font-size:clamp(1.25rem, 6vw, 1.5rem)}article.search-track section.left p.album{display:flex;justify-content:none;align-items:center;flex-direction:row;color:#d7d7d7;font-size:clamp(0.8rem, 4vw, 1rem)}article.search-track section.left p.album span{margin-right:.25em;display:block;height:25px;width:25px;color:#f5f5f5}article.search-track section.rating{display:flex}article.search-track section.rating div.rate{margin-left:.25em;aspect-ratio:1;display:flex;justify-content:center;align-items:center;flex-direction:column;background:rgba(20,20,20,.65);border-radius:.15em;padding:.5em 1em}article.search-track section.rating div.rate span{display:block;height:clamp(20px,8vw,40px);width:clamp(20px,8vw,40px)}article.search-track section.rating div.rate p{font-weight:500;font-size:clamp(0.8rem, 4vw, 1rem)}@media(max-width: 768px){article.search-track section.rating div.rate{flex-grow:1;aspect-ratio:initial;padding:.5em}}@media(max-width: 768px){article.search-track{padding:.5em;flex-direction:column}article.search-track section.left{width:100%;margin-bottom:1em}article.search-track section.rating{width:100%}}